<template>
  <div class="bodyBox">
    <div class="header flex-3">
      <img
        class="header-img"
        src="@/assets/background/header.png"
      >
      <div class="title-assistant flex-center flex-1">
        <img
          src="@/assets/svg/aiAssistant.svg"
          style="margin-right:0.75vw;width: 2.5vw;height:2.5vw;-webkit-user-drag: none;"
        >
        <div style="letter-spacing: 0.05vw;">您好，我是你的智能运营助手</div>
      </div>
      <div class="flex-center flex-2 main-title">139邮箱反垃圾邮件系统</div>
      <div
        class="flex-center flex-1 title-menu"
        style="z-index: 1;margin-top: 1.5vw"
      >
        <div
          v-if="selectKey==='/home'"
          class="icon-select flex-center"
        >
          <HomeFilled style="color:white;cursor: pointer;" />
        </div>
        <div
          v-else
          class="icon-unselect flex-center"
        >
          <HomeOutlined
            @click="onMenuSelect('/home')"
            style="color:rgb(147, 210, 243)"
          />
        </div>
        <div class="icon-unselect flex-center">
          <AppstoreOutlined style="color:rgb(147, 210, 243)" />
        </div>
        <div
          v-if="selectKey==='/mail/index'"
          class="icon-select flex-center"
        >
          <MailFilled
            style="color:white;cursor: pointer;"
            @click="onMenuSelect('/mail/index')"
          />
        </div>
        <div
          v-else
          class="icon-unselect flex-center"
        >
          <MailOutlined
            @click="onMenuSelect('/mail/index')"
            style="color:rgb(147, 210, 243)"
          />
        </div>
        <div class="icon-unselect flex-center">
          <DatabaseOutlined style="color:rgb(147, 210, 243)" />
        </div>
        <div class="header-hello">{{userName}}，你好</div>
      </div>
    </div>
    <div class="content">
      <router-view />
    </div>
    <div class="flex-2"></div>
    <div class="footer">
      <img
        class="footer-img"
        src="@/assets/background/footer.png"
      >
    </div>
  </div>
</template>

<script>
import { MailOutlined, HomeOutlined, HomeFilled, MailFilled, DatabaseOutlined, AppstoreOutlined } from '@ant-design/icons-vue'
export default {
  name: 'BasicLayout',
  components: { MailOutlined, HomeOutlined, HomeFilled, MailFilled, DatabaseOutlined, AppstoreOutlined },
  data () {
    return {
      userName: '陈先生',
      selectKey: ''
    }
  },
  mounted () {
    this.selectKey = this.$route.path
    if (this.selectKey.includes('/mail/mailDetail') || this.selectKey.includes('/mail/userDetail')) {
      this.selectKey = '/mail/index'
    }
  },
  methods: {
    onMenuSelect (path) {
      this.selectKey = path
      this.$router.push(path)
    }
  }
}
</script>

<style lang="scss" scoped>
.bodyBox {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  .header {
    position: relative;
    display: flex;
    .header-img {
      position: absolute;
      width: 100%;
      z-index: 0;
    }
    .title-assistant {
      position: relative;
      top: 0.75vw;
      color: white;
      font-weight: bold;
      font-size: calc(0.2em + 1vw);
      font-family: SourceHanSansSC;
    }
    .main-title {
      color: white;
      font-size: calc(0.5em + 1vw);
      font-weight: bold;
      font-family: SourceHanSansSC;
      z-index: 1;
      margin-top: 0.5vw;
      letter-spacing: 0.03vw;
    }
    .title-menu {
      .header-hello {
        margin-left: 1vw;
        color: white;
        font-weight: bold;
        font-size: calc(0.2em + 1vw);
        font-family: SourceHanSansSC;
        z-index: 1;
        letter-spacing: 0.05vw;
      }
      .icon-select {
        min-width: 2.5vw;
        :deep(svg) {
          width: 1.8vw;
          height: 1.8vw;
        }
      }
      .icon-unselect {
        min-width: 2.5vw;
        :deep(svg) {
          width: 1.2vw;
          height: 1.2vw;
        }
      }
    }
  }
  .content {
    flex: 16;
    display: flex;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  .footer {
    position: absolute;
    bottom: -4vw;
    width: 100%;
    text-align: center;
    .footer-img {
      width: 50%;
      -webkit-user-drag: none;
    }
  }
}
</style>