import { createRouter, createWebHashHistory } from 'vue-router'
import { BasicLayout } from '@/layouts'

export const routes = [
  {
    path: '/',
    name: 'index',
    component: BasicLayout,
    meta: { title: 'index' },
    redirect: '/home',
    children: [
      {
        path: '/home',
        name: 'home',
        component: () => import('@/views/home/index.vue'),
        meta: { title: 'home', label: 'home' }
      },
      {
        path: '/mail',
        name: 'mail',
        redirect: '/mail/index',
        children: [
          {
            path: '/mail/index',
            name: 'mailIndex',
            component: () => import('@/views/mail/index.vue'),
            meta: { title: '邮件详情', label: '邮件详情' }
          }, {
            path: '/mail/mailDetail/:id?',
            name: 'mailDetail',
            component: () => import('@/views/details/mail.vue'),
            meta: { title: '邮件详情', label: '邮件详情' }
          }, {
            path: '/mail/userDetail/:id?',
            name: 'userDetail',
            component: () => import('@/views/details/user.vue'),
            meta: { title: '用户详情', label: '用户详情' }
          }
        ]
      }
    ]
  },
  {
    path: '/404',
    component: () => import('@/views/exception/404.vue')
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export function resetRouter () {
  const newRouter = createRouter()
  router.matcher = newRouter.matcher
}

export default router
